        font-size: 45px;
      }
    h5 {
        font-size: 40px;
      }

    .align-center {
        text-align: center;
      }
    .text {
        word-wrap: break-word;
        position: relative;
      }
    .text-2 {
        font-size: 20px;
        width: 755px;
        margin: 20px auto 0;
    }
    .text-3 {
        font-size: 30px;
        width: 755px;
        margin: 20px auto 0;
    }
    .text-6 {
        margin: 20px 0 0;
        color: #afaeae  powderblue
    }
    .text-yellow{
        color: yellow; 
    }
    .icons {
      display: flex;
      justify-content: end;
      align-items: center;
    }
    .icons p {
        font-size: 32px;
    }
    .icons a {
        color: var(--color-transparent-black);
        margin-right: 30px;
    }
    .icons a:hover {
        color: var(--color-grey);
    }
    textarea {
      font-size: 18px; 
    }
    #frontPage {
        flex: 1; 
      }
    .frontPageBtn {
        display: flex;
        gap: 20px;
        align-items: baseline;
        justify-content: space-evenly;
        margin-top: 40px; 
        margin-bottom: 80px;
      }
      
    .btnArea {
        height: 250px;
        width: 200px;
        padding: 20px;
        border: 1px solid #ccc;
        background-color: #3f3f3f;
        cursor: pointer;
        
        h6 {
            font-size: 30px;
            margin-top: 0px;
            text-align: center;
          }  
        .icon {
            display: block;
            line-height: 0;
            border-width: 0px;
        }
        .icon-circle {
            border-radius: 50%;
            background: #2a63ec;
            width: 30px;
            height:30px;
            background-image: none;
            margin: 0 0 0 auto;
            padding: 19px;
        }
        .icon-1 {
            width: 30px;
            height: 30px;
            font-size: 35px; 
            color: white; 
            display: flex;
            align-items: center;
            justify-content: center;
          }
    }
    #navBtn {
        position: fixed;
        top: 45%;
        left: 30px;
        .navSubBtn {
            background: #2a63ec;
            border: 0px solid #2a63ec;
            border-radius: 35px;
            font-size: 15;
            font-weight: bold;
            color: #fff;
            display: flex;
            align-items: center;
            justify-content: center;
            cursor: pointer;
            outline: none;
            transition: all .25s ease;
            width: 60px;
            position: relative;
            height: 60px;
            overflow: hidden;
            margin-bottom: 20px;

            &:hover {
                width: 200px; 
              }
            &:hover .text {
                opacity: 1;
              }
        }
 
        .navSubBtn:not(.loading):hover {
            box-shadow: 0px 10px 25px 0px rgba(73, 215, 92,.4);
        }
        .navSubBtn:not(.loading):hover i {
            transform: translate(7px);
        }
        .navSubBtn i {
            font-size: 20;
            position: absolute;
            left: 0px;
            pointer-events: none;
            z-index: 10;
            background: inherit;
            width: 60px;
            height: 60px;
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 50%;
            transition: transform  .25s ease;

        }        
        .text {
            width: 80px;
            display: block;
            position: relative;
            pointer-events: none;
            transition: all .25s ease;
            position: absolute;
            left: 60px;
            opacity: 0; 
            transition: opacity 0.25s ease; 
        }

    }
    .user-input-table {
      border-collapse: collapse;
      width: 100%;
      align-items: center;
      border-bottom: 1px solid #ddd;

      tr {
        border-bottom: 1px solid #ddd;

      }
      td {
          padding: 8px;
        }

      label {
          font-weight: bold;
          display: block;
          margin-bottom: 4px;
        }
      input{
          width: 100%;
          padding: 4px 8px;
          border: 1px solid #ccc;
          border-radius: 4px;
          box-sizing: border-box;
        }
      select{
          width: 150;
          border: 1px solid #ccc;
          border-radius: 4px;
          box-sizing: border-box;
        }
      
      input[type="text"] {
          width: 200px;
        }
      
      input[type="month"] {
          width: 140px;
        }
      
        input[type="number"] {
          width: 100px;
          text-align: right;
        }
        .searchCriteriaBtn {
          background-color: #2a63ec;
          color: #fff;
          border: none;
          font-size: 20px;
          cursor: pointer;
          border-radius: 4px;
          align-items: center;
          width: 300px;
          height: 40px;
          display: flex;
          justify-content: center;
          align-items: center;
          i {
            padding: 10px;
          }
      }
        
      .searchCriteriaBtn:hover {
          background-color: #1e449c;
      }
